export const attachTodoActionsListeners = () => {
    const editTodoBtns = document.querySelectorAll('.edit-todo');
    const deleteTodoBtns = document.querySelectorAll('.delete-todo');
    const completeTodoBtns = document.querySelectorAll('.complete-todo');
    if (completeTodoBtns) {
        completeTodoBtns.forEach((completeTodoBtn) => completeTodoBtn.addEventListener('click', (event) => {
            const todoId = event.target.dataset['id'];
            const projectTitle = document.querySelector('.project-nav__btn.active').dataset['project'];
            if (!todoId || !projectTitle)
                return;
            handleCompleteTodo(todoId, projectTitle);
        }));
    }
    if (editTodoBtns) {
        editTodoBtns.forEach((editTodoBtn) => editTodoBtn.addEventListener('click', () => {
            const todoId = editTodoBtn === null || editTodoBtn === void 0 ? void 0 : editTodoBtn.dataset['id'];
            const app = document.querySelector('.app');
            const modal = document.querySelector('.modal');
            const modalBackdrop = document.querySelector('.modal-backdrop');
            app.classList.add('show');
            modal.classList.add('show');
            modalBackdrop.classList.add('show');
            if (!todoId) {
                return;
            }
            const projectTitle = document.querySelector('.project-nav__btn.active').dataset['project'];
            const selectedProject = convertedData().find((project) => project.title === projectTitle);
            const selectedTodo = selectedProject.todos.find((todo) => todo.id === todoId);
            if (!projectTitle || !selectedProject || !selectedTodo)
                return;
            UI.renderForm('EDIT_TODO', selectedTodo);
            attachCancelBtnListener();
            attachEditSubmitListener();
        }));
    }
    if (deleteTodoBtns) {
        deleteTodoBtns.forEach((deleteBtn) => deleteBtn.addEventListener('click', (event) => {
            const todoId = event.target.dataset['id'];
            const projectTitle = document.querySelector('.project-nav__btn.active').dataset['project'];
            if (!todoId || !projectTitle)
                return;
            handleDeleteTodo(todoId, projectTitle);
        }));
    }
};
export const attachNavListener = () => {
    const todolist = document.querySelector('.todolist');
    const navBtns = document.querySelectorAll('.project-nav__btn');
    navBtns.forEach((navBtn) => navBtn.addEventListener('click', (event) => {
        const activeBtn = document.querySelector('.project-nav__btn.active');
        const projectTitle = event.target.dataset['project'];
        const selectedProject = convertedData().find((project) => project.title === projectTitle);
        if (activeBtn && activeBtn !== navBtn) {
            activeBtn.classList.remove('active');
            navBtn.classList.toggle('active');
        }
        handleNavChange(todolist, selectedProject);
    }));
};
