const handleAddFormSubmit = () => {
    const title = document.getElementById('title').value;
    const date = document.getElementById('due').value;
    const priority = document.getElementById('priority')
        .value;
    const projectTitle = document.getElementById('project')
        .value;
    const description = document.getElementById('description').value;
    const selectedProject = convertedData().find((project) => project.title === projectTitle);
    if (!selectedProject)
        return;
    if (title && date && priority && description) {
        selectedProject === null || selectedProject === void 0 ? void 0 : selectedProject.addTodo(new Todo(title, description, Number(priority), date, projectTitle));
    }
    const newData = convertedData().map((project) => {
        if (project.title === projectTitle) {
            Object.assign(project, selectedProject);
        }
        return project;
    });
    setData(newData);
    UI.removeChildren(document.querySelector('.project-nav'));
    UI.removeChildren(document.querySelector('.todolist'));
    UI.renderNavList(newData);
    UI.renderTodolist(selectedProject === null || selectedProject === void 0 ? void 0 : selectedProject.todos);
    attachNavListener();
    attachShowTodoListener();
    attachTodoActionsListeners();
};
