const handleDeleteTodo = (todoId, projectTitle) => {
    const selectedProject = convertedData().find((project) => project.title === projectTitle);
    selectedProject.deleteTodo(todoId);
    const newData = convertedData().map((project) => {
        if (project.title === projectTitle) {
            Object.assign(project, selectedProject);
        }
        return project;
    });
    setData(newData);
    UI.removeChildren(document.querySelector('.project-nav'));
    UI.removeChildren(document.querySelector('.todolist'));
    UI.renderNavList(newData);
    UI.renderTodolist(selectedProject === null || selectedProject === void 0 ? void 0 : selectedProject.todos);
    attachNavListener();
    attachShowTodoListener();
    attachTodoActionsListeners();
};
const handleCompleteTodo = (todoId, projectTitle) => {
    const selectedProject = convertedData().find((project) => project.title === projectTitle);
    const selectedTodo = selectedProject.todos.find((todo) => todo.id === todoId);
    const projectComplete = convertedData().find((project) => project.title === 'Completed');
    if (!selectedProject || !selectedTodo || !projectComplete)
        return;
    selectedProject.completeTodo(todoId);
    projectComplete.addTodo(selectedTodo);
    selectedProject.deleteTodo(todoId);
    const newData = convertedData().map((project) => {
        if (project.title === selectedProject.title) {
            project = Object.assign(project, selectedProject);
        }
        else if (project.title === projectComplete.title) {
            project = Object.assign(project, projectComplete);
        }
        return project;
    });
    setData(newData);
    UI.removeChildren(document.querySelector('.project-nav'));
    UI.removeChildren(document.querySelector('.todolist'));
    UI.renderNavList(newData);
    UI.renderTodolist(selectedProject === null || selectedProject === void 0 ? void 0 : selectedProject.todos);
    attachNavListener();
    attachShowTodoListener();
    attachTodoActionsListeners();
};
const handleNavChange = (todolistEl, selectedProject) => {
    if (todolistEl) {
        UI.removeChildren(todolistEl);
    }
    UI.renderTodolist(selectedProject === null || selectedProject === void 0 ? void 0 : selectedProject.todos);
    attachShowTodoListener();
    attachTodoActionsListeners();
};
